/* OneMoreThink.css */
.onemorethink-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #000; /* Black background */
    height: 100%;
    width: 100%;
    justify-content: center;
    padding: 20px; /* Add padding for smaller screens */
  }
  
  /* Text section */
  .onemorethink-text-section {
    text-align: center; /* Center-align the text */
    margin-bottom: 20px; /* Space between text and image */
  }
  
  .onemorethink-header-text {
    font-size: 5vw; /* Responsive font size */
    font-weight: bold;
    color: white;
    margin: 0;
  }
  
  .onemorethink-subheader-text {
    font-size: 1.5vw; /* Adjusts with screen width */
    color: #AAA; /* Light gray subheader text */
    margin: 0;
    padding-top: 10px;
  }
  
  /* Image section */
  .onemorethink-image-section {
    margin-top: 20px;
  }
  
  .onemorethink-product-image {
    width: 70vw; /* Default width for larger screens */
    max-width: 1200px; /* Cap image size on large screens */
    height: auto;
    object-fit: contain; /* Ensure the image retains aspect ratio */
}

/* Responsive Design: Adjustments for smaller screens */
@media (max-width: 768px) {
    .onemorethink-header-text {
        font-size: 8vw; /* Slightly larger text for small screens */
    }

    .onemorethink-subheader-text {
        font-size: 3.5vw; /* Increase subheader size for smaller screens */
    }

    .onemorethink-product-image {
        width: 100vw; /* Full width image for small screens */
        height: auto; /* Maintain aspect ratio */
        max-width: none; /* Remove max width restriction */
    }
}

@media (max-width: 480px) {
    .onemorethink-header-text {
        font-size: 9vw; /* Bigger text for extra small screens */
    }

    .onemorethink-subheader-text {
        font-size: 4.5vw; /* Increase subheader size even more */
    }

    .onemorethink-product-image {
        width: 100vw; /* Full width image for extra small screens */
        height: auto; /* Maintain aspect ratio */
        max-width: none; /* Remove max width restriction */
    }
}
